@import "./Variables.scss";
@import "./extend.scss";
header {
  border-bottom: 2px solid $orange;
  .navbar {
    width: 100%;
    padding: 0rem 1rem;
    background-color: $white !important;
    .navbar-brand {
      padding: 0;
      img {
        max-height: 45px;
      }
    }
    .navbar-nav {
      a {
        color: $orange !important;
        height: 64px;
        line-height: 64px;
        padding: 0 16px !important;
        font-size: 17px;
        -webkit-transition: color 0.15s;
        transition: color 0.15s;
        &:after {
          display: none;
        }
      }
      .ad {
        padding: 20px 16px;
        position: relative;
        .nav-link {
          background-color: transparent;
          border: 0;
          border-radius: 2px;
          font-size: 14px;
          height: 24px;
          line-height: 24px;
          padding: 0 16px;
          vertical-align: middle;
          z-index: 0;
          -webkit-box-shadow: inset 0 0 0 1px #fb813f, inset 0 0 0 2px transparent, inset 0 0 0 3px transparent;
          box-shadow: inset 0 0 0 1px #fb813f, inset 0 0 0 2px transparent, inset 0 0 0 3px transparent;
          &:hover {
            background-color: rgba(251, 129, 63, 0.1);
            webkit-box-shadow: inset 0 0 0 1px #fb813f, inset 0 0 0 2px #fb813f, inset 0 0 0 3px transparent;
            box-shadow: inset 0 0 0 1px #fb813f, inset 0 0 0 2px #fb813f, inset 0 0 0 3px transparent;
          }
        }
        &:before {
          content: "";
          bottom: 16px;
          float: left;
          height: 32px;
          left: 0;
          position: absolute;
          top: 16px;
          width: 1px;
          background: rgba(0, 0, 0, 0.15);
        }
      }
      .dropdown {
        &:hover {
          .dropdown-menu {
            display: block !important;
          }
        }
        .dropdown-menu {
          background-color: #fff;
          box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 6px 9px rgba(0, 0, 0, 0.2);
          border-radius: 0 0 2px 2px;
          padding: 4px 0;
          width: 272px;
          border: 0;
          left: 0;
          top: 62px;
          &:after {
            content: "";
            width: 15px;
            height: 15px;
            display: block;
            border: 1px solid rgba(0, 0, 0, 0.1);
            position: absolute;
            top: -8px;
            background: $white;
            transform: rotate(45deg);
            left: 50px;
            border-width: 1px 0px 0 1px;
          }
          .dropdown-item {
            border: 0;
            line-height: 32px;
            font-weight: 600;
            color: rgba(0, 0, 0, 0.55);
            font-size: 15px;
            display: block;
            height: 32px;
            padding: 0 16px !important;
            &:hover {
              color: $orange !important;
            }
          }
        }
      }
    }
  }
}
.banner-section {
  position: relative;
  img {
    width: 100%;
  }
  .banner-content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 100%;
    .banner-content-container {
      h1 {
        color: $white;
        line-height: 72px;
        font-weight: 300;
        font-size: 66px;
        font-family: $font-pt;
        margin: 0;
      }
      p {
        line-height: 32px;
        font-weight: 300;
        color: $banner-p-col;
        font-size: 26px;
        font-family: $font-pt;
        margin: 0;
      }
      .banner-actions {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .banner-action {
          @extend %button;
          background: $white-btn-bg;
          color: $black;
          margin: 12px 8px 0px;
        }
      }
    }
  }
}

.parbase-section {
  padding: $padding-top-bot;
  background-color: #fbfbfbf2;
  padding-bottom: 64px;
  h4 {
    font-size: $font-s-26;
    color: $dark-grey;
    font-weight: 300;
    padding: $padding-top-bot;
  }
  img {
    width: 72px;
    margin: 0 auto 0;
  }
  .card {
    border: 1px solid $light-grey-border;
    padding: $padding-top-bot;
    .card-title {
      font-size: $font-s-20;
      font-weight: 400;
      line-height: 28px;
      margin: 0;
    }
    p {
      font-size: $card-text;
      line-height: 24px;
      margin-top: 10px;
      color: $dark-grey;
    }
    .card-btn {
      @extend %button;
      background-color: $card-btn-bg;
      color: $dark-grey;
      &:hover {
        background-color: $card-btn-bg-hover;
      }
    }
  }
}
.create-Title {
  margin: 5px 0;
  h4 {
    @extend %home-heading;
    span {
      @extend %home-heading-span;
    }
  }
}
.create-ad {
  position: relative;
  padding: $padding-top-bot;
  .rich-text {
    .top-section {
      @extend %button-effects;
      &:before,
      &:after {
        content: "";
        width: 0;
        height: 2px;
        position: absolute;
        transition: all 0.2s linear;
        background: $orange;
        left: 0;
        transition-duration: 0.3s;
      }
      &:before {
        top: 0;
        width: 15%;
      }
      &:after {
        bottom: 0;
        width: 40%;
      }
      span {
        @extend %button-effects-span;
        &:before,
        &:after {
          content: "";
          width: 2px;
          height: 0;
          position: absolute;
          transition: all 0.2s linear;
          background: $orange;
          top: 50%;
          transition-duration: 0.3s;
        }
        &:before {
          top: 0;
          height: 100%;
          left: 0;
        }
        &:after {
          right: 0;
        }
      }
      &:hover {
        &:before,
        &:after {
          width: 100%;
          left: 0;
        }
        span {
          &:after {
            height: 100%;
            top: 0;
          }
        }
      }
    }
  }
  .bottom-contect {
    padding: $padding-top-bot;
    h2 {
      line-height: 35px;
      font-weight: 300;
      color: rgba(0, 0, 0, 0.85);
      font-size: 30px;
      margin-bottom: 15px;
    }
    p {
      line-height: 24px;
      font-weight: 400;
      color: $text;
      font-size: $card-text;
    }
    .card-btn {
      font-weight: 400;
      color: $orange;
      font-size: $font-s-15;
      text-decoration: none;
    }
  }
  img {
    width: 100%;
  }
  .cnt-section {
    padding: $padding-top-bot;
    position: absolute;
    top: 32px;
    left: 0;
    right: 0;
  }
}

.goal {
  padding: 0;
  background-color: #fff;
  img {
    width: 329px;
  }
}
